@import url(https://fonts.googleapis.com/css?family=Montserrat:900i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: left;
          justify-content: left;
  background-color: #373737;
}

.nav-link {
  color: #FCE694
}

.nav-link:hover {
  color: #C7DFC5
}

.navbar__item {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
  list-style-type: none;
}

.container {
  background: #ffffff;
  display: grid;
  height: 75vh;
  width: 75vh;
  padding: 0;
}

.sketchpadContainer {
  text-align: center;
}

.grid {
  border: 1px solid #000000;
}

.color {
  background: #000000;
}

.sketch__input {
  border: 1px #373737 solid;
  margin: 10px;
  outline: none;
  width: 50vh;
  text-align: center;
  border-radius: 5px;
}

.btn__settings { 
  display: inline-block;
  position: relative;
  right: 50vh;
  bottom: 60vh;
}

.grid__newSketch,
.grid__save,
.grid__mode,
.grid__color,
.grid__mode:active:focus,
.grid__mode:focus,
.grid__mode:hover {
  background-color: #C7DFC5 !important;
  border: 1px #373737 solid !important;
  color: #373737;
  box-shadow: none !important;
  width: 18vh;
  margin: 10px 5px;
}

.grid__newSketch:hover,
.grid__newSketch:focus,
.grid__newSketch:active:focus,
.grid__save:hover,
.grid__save:focus,
.grid__save:active:focus,
.grid__color:hover,
.grid__color:focus,
.grid__color:active:focus {
  background-color: #373737 !important;
  color: #FCE694;
}

.selected,
.selected:hover,
.selected:active:focus,
.selected:focus {
  background-color: #373737 !important;
  color: #FCE694;
}
.previewContainer {
  background: #ffffff;
  display: grid;
  height: 20vh;
  width: 20vh;
  padding: 0;
  margin: 0 auto;
  margin-bottom: 10px;
}

.grid {
  border: 1px solid #000000;
}

.btn-edit,
.btn-delete {
  background-color: #C7DFC5;
  border: 1px #373737 solid !important;
  color: #373737;
  width: 20vh;
  margin: 5px;
  box-shadow: none !important
}

.btn-edit:hover,
.btn-delete:hover,
.btn-edit:focus,
.btn-delete:focus,
.btn-edit:active:focus,
.btn-delete:active:focus {
  background-color: #373737 !important;
  color: #FCE694;
}

.sketchCard {
  color: #373737;
  margin: 20px 10px;
  text-align: center;
  width: 50vh;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.sketchCard__card {
  background-color: #FCE694;
}

.sketches {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 10px;
}
.formContainer {
  height: 100vh;
  margin: 40vh auto;
  float: none
}

.form_btn {
  background-color: #C7DFC5;
  border: 1px #373737 solid;
  color: #373737;
  box-shadow: none !important;
}

.form_btn:hover,
.form_btn:focus,
.form_btn:active:focus {
  background-color: #373737 !important;
  color: #FCE694;
}

.form_label {
  background-color: #FCE694;
  color: #373737; 
  border: 1px #373737 solid;
}

.form_input,
.form_input:active {
  border: 1px #373737 solid !important;
  box-shadow: none !important;
}

.logo {
  position: absolute;
  top: 25%;
  left: 50%;
  font-family: 'Montserrat', sans-serif;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size:120px;
	letter-spacing:0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: white;
  text-shadow: 
						8px 8px #FCE694,
						20px 20px #000000;
}
body {
  background-color: #C1DBE3;
  overflow: hidden;
}
